import { useState, useEffect } from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import UserService from "~/services/UserService";
import { Delete, Edit, PersonAdd } from "@mui/icons-material";
import { Button, IconButton, Box } from "@mui/material";
import styles from "./UsersTable.module.css";
import { UserDelete } from "../UserDelete";
import { useNavigate } from "react-router-dom";

import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { setLoading } from '~/state/edit/editState';

export const UsersTable = () => {
  const { t } = useTranslation("manage");
  const navigate = useNavigate();
  const [users, setUsers] = useState([]);
  const dispatch = useDispatch();

  const getUsers = () => {
    dispatch(setLoading(true));
    UserService.getAllUsers()
      .then((res) => {
        if (res) {
          setUsers(res);
        }
      })
      .finally(() => {
        dispatch(setLoading(false));
      });
  };

  const [userToDelete, setUserToDelete] = useState(null);
  const onCloseModal = () => {
    setUserToDelete(null);
  };
  const deleteUser = () => {
    UserService.deleteUser({ userId: userToDelete.id })
      .then(() => {
        onCloseModal();
        getUsers();
      })
      .catch((e) => {
        onApiError({
          error: e,
          globalErrorHandler: (processedError) => {
            dispatch(onError(processedError));
          },
        });
      });
  };

  useEffect(() => {
    getUsers();
  }, []);

  return (
    <>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell align="left">{t("users_manage.name")}</TableCell>
              <TableCell align="left">{t("users_manage.email")}</TableCell>
              <TableCell align="left">{t("users_manage.roles")}</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {users.map((user) => {
              return (
                <TableRow key={user.id}>
                  <TableCell component="th" scope="row">
                    <IconButton
                      title={t("users_manage.title_delete")}
                      onClick={() => {
                        setUserToDelete(user);
                      }}
                    >
                      <Delete />
                    </IconButton>
                    <IconButton
                      title={t("users_manage.title_edit")}
                      onClick={() => {
                        navigate(`/edit-user/${user.id}`);
                      }}
                    >
                      <Edit />
                    </IconButton>
                  </TableCell>
                  <TableCell align="left">
                    {user.firstName} {user.lastName}
                  </TableCell>
                  <TableCell align="left">{user.email}</TableCell>
                  <TableCell align="left">
                    {user.roles.map((role) => t(`label.${role}`)).join(", ")}
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>

      <Button
        className={styles.addUser}
        onClick={() => {
          navigate("/add-user");
        }}
      >
        <PersonAdd />
        {t("users_manage.add_user")}
      </Button>
      {Boolean(userToDelete) && (
        <UserDelete
          deleteUser={deleteUser}
          name={userToDelete.firstName + " " + userToDelete.lastName}
          open={Boolean(userToDelete)}
          onClose={onCloseModal}
        />
      )}
    </>
  );
};
